body {
    font-family: 'Open Sans', sans-serif;
    background-color: #f3f3f3;
    color: #3b3b3b;
    font-size: 1.2em;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    overflow-wrap: break-word;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Baloo', cursive;
    font-weight: unset
}

a {
    cursor: pointer;
    text-decoration: none
}

.section-container {
    max-width: 1000px;
    margin: 0 auto
}

.container-padding {
    padding: 1em
}

.footer-text {
    text-align: center;
    margin: 0.5em
}


@keyframes fade-in {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes zoom-out {
    from {
        transform: scale(1.5)
    }
    to {
        transform: scale(1)
    }
}

@keyframes to-right {
    from {
        transform: translateX(2em)
    }
    to {
        transform: translateX(0)
    }
}
